.header {
  background-color: #f0f0f0;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  animation: 1s ease-out forwards slide-in;
  display: flex;
  box-shadow: 0 4px 8px #0000001a;
}

.logo-container {
  flex: 1;
}

.logo {
  width: auto;
  height: 80px;
  transition: transform .3s ease-in-out;
}

.logo:hover {
  transform: rotate(360deg);
}

.nav-items {
  flex: 1.5;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.nav-items ul {
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-items li {
  text-transform: uppercase;
  cursor: pointer;
  font-size: 18px;
  font-weight: 500;
  transition: color .3s ease-in-out;
}

.nav-items li a {
  color: #333;
  text-decoration: none;
  transition: color .3s;
}

.nav-items li:hover a {
  color: tomato;
}

button.login {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .4s, transform .3s;
}

button.login:hover {
  background-color: tomato;
  transform: scale(1.1);
}

@keyframes slide-in {
  0% {
    opacity: 0;
    transform: translateY(-50%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.body {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.filter {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.search-box {
  border: 2px solid #ccc;
  border-radius: 5px;
  width: 250px;
  padding: 10px;
  font-size: 16px;
  transition: border-color .3s ease-in-out;
}

.search-box:focus {
  border-color: #007bff;
  outline: none;
}

.search-btn {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s, transform .2s;
}

.search-btn:hover {
  background-color: tomato;
  transform: scale(1.05);
}

.filter-btn {
  color: #fff;
  cursor: pointer;
  background-color: #28a745;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s, transform .2s;
}

.filter-btn:hover {
  background-color: #218838;
  transform: scale(1.05);
}

.res-container {
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  display: grid;
}

.res-card {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 4px 12px #0000001a;
}

.res-logo {
  object-fit: cover;
  border-radius: 8px;
  width: 100%;
  height: 150px;
  margin-bottom: 15px;
}

.res-card h3 {
  color: #333;
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.res-card h4 {
  color: #666;
  margin-bottom: 5px;
  font-size: 1rem;
}

@media (width <= 768px) {
  .filter {
    flex-direction: column;
    align-items: stretch;
  }

  .search-btn, .filter-btn {
    width: 100%;
    margin-top: 10px;
  }

  .res-container {
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  }
}

.menu {
  background-color: #f9f9f9;
  border-radius: 10px;
